import React from "react";
import myStyle from "./galleryStyle.module.css";

const content = () => {
  const menuBar = () => {
    let line2 = document.getElementById("ani");
    line2.style.width = "30px";
  };
  const menuBarShort = () => {
    let line2 = document.getElementById("ani");
    line2.style.width = "15px";
  };

  return (
    <div>
      <header className={myStyle.header}>
        <h1>
          <a href="#">
            <img src="./img/logo.png" alt="정식품 로고" />
          </a>
        </h1>
        <ul className={myStyle.language}>
          <li>
            <a href="#">KOR</a>
          </li>
          <li>
            <a href="#">ENG</a>
          </li>
        </ul>
        <ul
          className={myStyle.menu}
          onMouseOver={menuBar}
          onMouseLeave={menuBarShort}
        >
          <li className="lineWrap">
            <div className={myStyle.line1}></div>
            <div className={myStyle.line2} id="ani"></div>
            <div className={myStyle.line3}></div>
          </li>
        </ul>
      </header>
      <main className={myStyle.main}>
        <ul className={myStyle.family}>
          <li>Nature &#38; People</li>
          <li>Eday Mall</li>
          <li>OSSE</li>
        </ul>
        <div className={myStyle.slideWrap}>
          <div className={myStyle.slide1}>
            <img src="./img/slideimg1.png" alt="올바른 푸드" />
          </div>
          <div className={myStyle.slide2}>
            <img src="./img/slideimg2.png" alt="건강한 사람" />
          </div>
        </div>
      </main>
    </div>
  );
};

export default content;
