import { createGlobalStyle } from "styled-components";
import Reset from "styled-reset";

const GlobalStyle = createGlobalStyle`
${Reset};

// 내가 작성한 style
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&family=Oleo+Script+Swash+Caps:wght@400;700&family=Roboto+Condensed:ital@0;1&display=swap');
a {text-decoration:none; color:#000};
`;

export default GlobalStyle;
