import React from "react";
import myStyle from "./galleryStyle.module.css";

const content = () => {
  return (
    <div>
      <div className={myStyle.grayBox}></div>
      <ul className={myStyle.intro}>
        <li>
          <h2>두유 역사의 첫 장을 연 세계적 식품 전문기업</h2>
        </li>
        <li>
          <p>
            정식품은 1973년 설립되어 지난 40여 년동안 한국 및 세계의 두유산업을
            선도해왔습니다. 생명존중정신을 바탕으로 인류건강문화에 기여하기 위해
            끊임없이 새로운 도전을 이어가며 식품문화 창달에 앞장서고 있습니다.
          </p>
        </li>
        <li>
          <a href="https://www.vegemil.co.kr/main/company/introduceCEO.aspx">
            <img src="./img/more.png" alt="아이콘" />
            기업소개 더보기
          </a>
        </li>
      </ul>
    </div>
  );
};

export default content;
