import React from "react";
import myStyle from "./galleryStyle.module.css";
const GalleryItem = ({ item }) => {
  // 구조 분해 방식을 통해 item 생략 가능
  const { imgUrl, title, arrow, des, url } = item;

  return (
    <li>
      <div className={myStyle.imgBox}>
        <img src={imgUrl} alt={title} />
      </div>

      <h3>
        <a href={url} target="_blank" rel="noopener noreferrer">
          {title}
          <img src={arrow} alt="화살표" className={myStyle.arrowImg} />
        </a>
      </h3>

      <p>{des}</p>
    </li>
  );
};

export default GalleryItem;
